







import {Component, Vue} from 'vue-property-decorator';

const dateFormat = require('dateformat');
const sha256 = require('sha256')
import axios from 'axios'
import WebSdkSessionType from "@/struct/WebSdkSessionType";
import WebSdkSettingsType from '@/struct/WebSdkSettingsType';
import WebSdkConstructorObjectType from '@/struct/WebSdkConstructorObjectType';
import WebSdkExampleType, {WebSdkExampleSCardType, WebSdkExampleScFieldsType, WebSdkPaymentResultType} from '@/struct/WebSdkExampleType';

@Component({
    components: {}
})
export default class SafeChargeWebSdk extends Vue {
    private UserID!: string
    private RequestID!: string

    private WebSdkSettings: WebSdkSettingsType = {
        secretKey: 'AIJ3Fb8DnzVRu6Vmz1DsjMIWxnCi6OkkxJ0ZADykl4Y9zwZgPDf3NQ5wDsei1wLH',
        merchantSiteId: '206546',
        merchantId: '3519442750909825951'
    }
    private WebSdkSession?: WebSdkSessionType
    private SafeChargeExample?: WebSdkExampleType
    private SafeChargeSCard?: WebSdkExampleSCardType


    private created(): void {
        this.WebSdkSession = undefined
        this.UserID = (localStorage.getItem('userID') ?? String(Math.floor(Date.now() / 1000)))
        localStorage.setItem('userID', this.UserID)
        this.RequestID = String(Math.floor(Date.now() / 1000))
    }

    private mounted(): void {
        (window as any).DoSubmit = this.OnSubmit
        this.InitWebSdk()
    }

    private InitWebSdk(): void {
        if (('SafeCharge' in window) == false) {
            setTimeout(() => this.InitWebSdk(), 300)
            return
        }
        const currentDate = dateFormat(new Date(), "yyyymmddHHMMss")
        axios.post('https://ppp-test.safecharge.com/ppp/api/v1/openOrder.do', {
            "merchantId": this.WebSdkSettings.merchantId,
            "merchantSiteId": this.WebSdkSettings.merchantSiteId,
            "clientUniqueId": this.UserID,
            "clientRequestId": this.RequestID,
            "currency": "USD",
            "amount": "10",
            "timeStamp": currentDate,
            "checksum": sha256(this.WebSdkSettings.merchantId + this.WebSdkSettings.merchantSiteId + this.RequestID + "10" + "USD" + currentDate + this.WebSdkSettings.secretKey)
        }).then(res => {
            this.WebSdkSession = res.data
        });

        // @ts-ignore
        // eslint-disable-next-line
        this.SafeChargeExample = SafeCharge({
            env: 'test',
            merchantId: this.WebSdkSettings.merchantId,
            merchantSiteId: this.WebSdkSettings.merchantSiteId,
        });

        const ScFields = this.SafeChargeExample?.fields({
            locale: 'en'
        })

        const style = {
            base: {
                fontSize: '16pt',
                fontFamily: 'Roboto, sans-serif',
                color: "#23262C",
                fontSmoothing: 'antialiased',
                '::placeholder': {
                    'font-size': '18px',
                    color: '#CECFD0'
                }
            },
            invalid: {
                color: '#FF6231',
                ':focus': {
                    color: '#FF6231'
                }
            },
            empty: {
                color: '#CECFD0',
                '::placeholder': {
                    color: '#CECFD0'
                }
            },
            valid: {
                color: '#23262C'
            }
        };

        this.SafeChargeSCard = ScFields?.create('card', {style: style});
        this.SafeChargeSCard?.attach(document.getElementById('safe-charge-web-sdk-iframe'));
    }

    private OnSubmit(cardHolderName: string): void {
        console.log('OnSubmit')
        try {
            this.SafeChargeExample?.createPayment({
                clientUniqueId: this.WebSdkSession?.clientUniqueId ?? "",
                sessionToken: this.WebSdkSession?.sessionToken ?? "",
                currency: "USD",
                amount: "10",
                cardHolderName: cardHolderName,
                paymentOption: this.SafeChargeSCard,
                // billingAddress: {
                //     country: "GB",
                //     email: "someemail@somedomain.com"
                // }
            }, (result: WebSdkPaymentResultType) => {
                alert("Response: " + result.result)
                console.log(result)
                this.$emit('finishPayment', result)
            })
        } catch (e) {
            console.log(e)
            this.$emit('finishPayment', undefined)
        }
    }
}
