



























































































import {Component, Vue} from 'vue-property-decorator';

import 'reset-css';

import "@fortawesome/fontawesome-free/less/fontawesome.less"
import "@/assets/style/fonts.less"
import "@/assets/style/common.less"

import "@/assets/style/buefy.custom.min.css"
import SafeChargeWebSdkForm from "@/components/SafeChargeWebSdkForm.vue";
import {WebSdkPaymentResultType} from "@/struct/WebSdkExampleType";

@Component({
    components: {SafeChargeWebSdkForm}
})
export default class Home extends Vue {
    private AcceptedTerns: boolean = false
    private CardHolderName: string = ""

    private ErrorAcceptedTerns: boolean = false
    private ErrorCardHolderName: boolean = false

    private HasSubmitted: boolean = false

    private created(): void {
        this.AcceptedTerns = true
        this.CardHolderName = "John Smith"

        this.ErrorAcceptedTerns = false
        this.ErrorCardHolderName = false

        this.HasSubmitted = false
    }

    private mounted() {
        //
    }

    private OnSubmit(): void {
        if (this.AcceptedTerns == false) {
            this.ErrorAcceptedTerns = true
            return
        }
        if (this.CardHolderName.trim().length == 0) {
            this.ErrorCardHolderName = true
            return
        }
        this.HasSubmitted = true;
        (window as any).DoSubmit(this.CardHolderName)
    }

    private OnFinishSubmit(result: WebSdkPaymentResultType | undefined): void {
        console.log('OnFinishSubmit')
        this.HasSubmitted = false
    }
}
